@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensansbold.woff2"), url("../fonts/opensansbold.woff");
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans.woff2"), url("../fonts/opensans.woff");
  font-weight: 400;
  font-style: normal;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
* {
  box-sizing: border-box;
}
body {
  font-family: "Open Sans", "Verdana", sans-serif;
  font-weight: 700;
  background-color: #ffffff;
  min-height: 100%;
  display: grid;
  grid-template-rows: auto 1fr auto;
}
a {
  text-decoration: none;
}
img {
  display: block;
}
