.reset-list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.reset-fieldset {
  border: none;
}
.page {
  height: 100%;
  overflow-x: hidden;
}
.page-body {
  margin: 0;
  min-height: 100%;
}
.page-body__container {
  width: 260px;
  margin: 0 auto;
}
.page-body__button {
  width: 260px;
  min-height: 52px;
  text-align: center;
  padding-top: 14px;
  padding-bottom: 14px;
}
.page-body__button:hover {
  background-color: #444444;
  color: #ffffff;
}
.page-body__button:focus {
  background-color: #444444;
  color: #ffffff;
}
.page-body__button:active {
  color: rgba(255, 255, 255, 0.3);
}
@media (min-width: 768px) {
  .page-body__container {
    width: 668px;
  }
  .page-body__button {
    width: 292px;
  }
}
@media (min-width: 1150px) {
  .page-body__container {
    width: 1050px;
  }
}
.page-header {
  z-index: 1;
  font-size: 17px;
  line-height: 24px;
  font-weight: 700;
}
.page-header__container {
  margin: 0 auto;
  width: 260px;
}
.page-header__container:hover,
.page-header__container:focus {
  opacity: 0.6;
}
.page-header__container:active {
  opacity: 0.3;
}
.page-header--modal-mode {
  z-index: 0;
}
@media (min-width: 768px) {
  .page-header {
    border-bottom: 1px solid #ececec;
  }
  .page-header__wrapper {
    display: grid;
    width: 668px;
    margin: 0 auto;
  }
  .page-header__container {
    margin: 0;
    width: fit-content;
  }
}
@media (min-width: 1150px) {
  .page-header {
    min-height: 89px;
  }
  .page-header__wrapper {
    width: 1050px;
  }
}
.page-main__title {
  position: relative;
  text-align: center;
  width: 260px;
}
.page-main__title-wrapper {
  background-color: #f1f1f1;
  min-height: 177px;
  padding-top: 73px;
  padding-bottom: 74px;
}
.page-main__title-wrapper--catalog {
  margin-bottom: 29px;
}
.page-main__title-wrapper--form {
  margin-bottom: 17px;
}
@media (min-width: 768px) {
  .page-main__title {
    width: 668px;
  }
  .page-main__title-wrapper {
    position: relative;
    padding-bottom: 85px;
    padding-top: 101px;
  }
  .page-main__title-wrapper::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 24px;
    bottom: -1px;
    background-image: url("../img/zigzag-fill-tablet.svg");
    background-repeat: repeat-x;
  }
  .page-main__title-wrapper--catalog {
    margin-bottom: 67px;
  }
  .page-main__title-wrapper--form {
    margin-bottom: 54px;
  }
}
@media (min-width: 1150px) {
  .page-main__wrapper {
    margin-top: -80px;
  }
  .page-main__title {
    margin-left: 175px;
    width: 700px;
    min-height: 114px;
  }
  .page-main__title-wrapper {
    padding-bottom: 56px;
  }
  .page-main__title-wrapper::after {
    height: 34px;
    bottom: 0;
    background-image: url("../img/zigzag-fill-desktop.svg");
  }
  .page-main__title-wrapper--catalog {
    margin-bottom: 110px;
  }
  .page-main__title-wrapper--form {
    margin-bottom: 97px;
  }
}
.page-footer {
  background-color: #f1f1f1;
  grid-row: -1 / -2;
}
.page-footer__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.page-footer__logo {
  display: none;
}
.page-footer__logo:hover svg {
  fill: #46c1ae;
}
.page-footer__logo:focus svg {
  fill: #46c1ae;
}
.page-footer__logo:active svg {
  opacity: 0.3;
}
.page-footer__social {
  width: 260px;
  padding-top: 24px;
  padding-bottom: 25px;
  border-bottom: 1px solid #e5e5e5;
}
.page-footer__copyright {
  width: 182px;
  display: flex;
  justify-content: space-between;
  padding-bottom: 23px;
  padding-top: 22px;
  height: 80px;
  align-items: center;
}
@media (min-width: 768px) {
  .page-footer__wrapper {
    min-height: 143px;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  .page-footer__logo {
    display: block;
    width: 102px;
    height: 41px;
  }
  .page-footer__social {
    width: 142px;
    padding-top: 22px;
    border-bottom: none;
  }
  .page-footer__copyright {
    width: 182px;
    padding-top: 17px;
    justify-content: space-between;
  }
}
@media (min-width: 1150px) {
  .page-footer__copyright {
    padding-top: 16px;
  }
}
.list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.link {
  color: #000000;
}
.link--white {
  color: #ffffff;
}
.link--product:hover {
  color: rgba(0, 0, 0, 0.6);
}
.link--product:active {
  color: rgba(0, 0, 0, 0.3);
}
.link--logo:hover {
  opacity: 0.6;
}
.title {
  margin: 0;
  padding: 0;
}
.title--general {
  font-size: 27px;
  line-height: 30px;
  color: #000000;
}
.title--fieldset {
  position: relative;
  font-size: 16px;
  line-height: 17px;
  text-transform: uppercase;
  color: #63d1bb;
  margin-bottom: 33px;
}
.title--fieldset-color {
  margin-bottom: 23px;
}
.title--fieldset-user-name {
  margin-bottom: 29px;
}
.title--fieldset-addition {
  margin-bottom: 22px;
}
.title--fieldset-special {
  position: relative;
  font-size: 16px;
  line-height: 17px;
  text-transform: uppercase;
  color: #63d1bb;
  margin-bottom: 4px;
}
.title--fieldset::after {
  content: "";
  position: absolute;
  right: 0;
  bottom: 9px;
  width: 200px;
  height: 1px;
  background-color: #62D1BA;
}
.title--centerman {
  margin: 0 auto;
}
@media (min-width: 768px) {
  .title--general {
    font-size: 43px;
    line-height: 50px;
  }
  .title--fieldset {
    font-size: 17px;
    line-height: 16px;
    margin-bottom: 0;
    align-self: flex-start;
    padding-top: 19px;
    padding-left: 2px;
  }
  .title--fieldset-user-name {
    padding-top: 0;
  }
  .title--fieldset-color {
    align-self: flex-start;
    margin-bottom: 0;
    padding-top: 15px;
  }
  .title--fieldset::after {
    display: none;
  }
}
@media (min-width: 1150px) {
  .title--general {
    font-size: 53px;
    line-height: 57px;
    margin: auto;
  }
  .title--fieldset,
  .title--fieldset-special {
    margin-left: 64px;
  }
  .title--fieldset-color {
    padding-top: 15px;
  }
  .title--fieldset-user-name {
    padding-top: 8px;
  }
}
.paragraph {
  margin: 0;
  padding: 0;
}
.paragraph--promo-price {
  margin-bottom: 25px;
}
.paragraph--promo-legend {
  margin-bottom: 27px;
}
.paragraph--form-lead {
  font-size: 13px;
  line-height: 24px;
  font-weight: 400;
}
.paragraph--form-notice {
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
}
.paragraph--modal {
  margin: 0 auto 11px auto;
}
@media (min-width: 768px) {
  .paragraph--promo-price {
    margin-bottom: 33px;
  }
  .paragraph--promo-legend {
    margin-bottom: 49px;
  }
  .paragraph--form-lead {
    font-size: 17px;
    line-height: 30px;
  }
  .paragraph--form-notice {
    font-size: 17px;
    line-height: 30px;
  }
}
@media (min-width: 1150px) {
  .paragraph--promo-legend {
    margin-bottom: 60px;
  }
  .promo__lead {
    padding-left: 35px;
    padding-top: 9px;
  }
  .promo__title {
    padding-left: 47px;
  }
}
.button {
  display: block;
  font-size: 18px;
  line-height: 20px;
  text-transform: uppercase;
  color: #444444;
  border: 2px solid #444444;
  font-weight: 700;
  background-color: transparent;
  cursor: pointer;
}
.button--form {
  font-size: 17px;
  line-height: 16px;
}
@media (min-width: 768px) {
  .button {
    font-size: 17px;
  }
  .button--form {
    width: 230px;
    margin-right: 108px;
  }
}
@media (min-width: 1150px) {
  .button--form {
    margin-right: 139px;
  }
}
.label--search-form:hover {
  opacity: 06;
}
@media (min-width: 768px) {
  .label--search-form {
    display: none;
  }
}
.form {
  font-size: 17px;
  line-height: 18px;
}
.form__wrapper {
  margin-bottom: 55px;
}
.form__lead {
  margin-bottom: 29px;
}
.form__fieldset-legend {
  line-height: 16px;
  text-transform: uppercase;
  color: #63d1bb;
}
.form__label {
  align-items: center;
}
.form__item--good-type {
  position: relative;
  display: flex;
}
.form__item--good-type:not(:last-child) {
  margin-bottom: 17px;
}
.form__radio-label {
  min-height: 36px;
}
.form__item--colour-type {
  position: relative;
  margin-bottom: 23px;
}
.form__label--set {
  cursor: pointer;
  display: flex;
  width: 165px;
  margin-left: 60px;
}
.form__radio-input + .form__radio-label::before {
  content: "";
  position: absolute;
  left: -1px;
  top: -5px;
  width: 38px;
  height: 38px;
  border-radius: 50%;
  border: 2px solid #444444;
}
.form__radio-input + .form__radio-label:hover::before {
  border: 2px solid #62D1BA;
}
.form__radio-input + .form__radio-label:focus::before {
  border: 2px solid #62D1BA;
}
.form__radio-input + .form__radio-label:active::before {
  border: 2px solid #444444;
  opacity: 0.3;
}
.form__radio-input:checked + .form__radio-label::after {
  content: "";
  position: absolute;
  left: 11px;
  top: 7px;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background-color: #62D1BA;
}
.form__checkbox-label {
  display: block;
  padding: 10px 0;
}
.form__checkbox-button + .form__checkbox-label::before {
  content: "";
  position: absolute;
  left: 0;
  top: 1px;
  width: 34px;
  height: 34px;
  border: 2px solid #444444;
}
.form__checkbox-button:checked + .form__checkbox-label::after {
  content: "";
  position: absolute;
  left: 8px;
  top: 12px;
  width: 24px;
  height: 16px;
  background-image: url("../img/checkbox-tick.svg");
  background-repeat: no-repeat;
  background-position: 0 0;
}
.form__checkbox-button + .form__checkbox-label:hover::before {
  border: 2px solid #62D1BA;
}
.form__checkbox-button + .form__checkbox-label:focus::before {
  border: 2px solid #62D1BA;
}
.form__checkbox-button + .form__checkbox-label:active::before {
  border: 2px solid #444444;
  opacity: 0.3;
}
.form__item--user-name {
  display: flex;
  flex-direction: column;
  margin-bottom: 23px;
}
.form__item--last {
  margin-bottom: 0;
}
.form__label--user-name {
  margin-bottom: 13px;
}
.form__input-container {
  position: relative;
}
.form__input-container--phone {
  position: relative;
}
.form__data-svg {
  position: absolute;
  right: 7px;
}
.form__data-svg--email {
  top: 4px;
  right: 3px;
}
.form__data--special {
  border-bottom: 2px solid #000000;
  padding-bottom: 16px;
  padding-top: 14px;
}
.form__data--special:hover,
.form__data--special:active,
.form__data--special:focus {
  border-bottom: 2px solid #62D1BA;
}
.form__textarea {
  width: 260px;
  min-height: 142px;
  padding-top: 15px;
  padding-left: 15px;
  line-height: 18px;
  border: 2px solid #000000;
}
.form__textarea:hover,
.form__textarea:active,
.form__textarea:focus {
  outline: none;
  border: 2px solid #62D1BA;
}
.form__notice {
  margin-bottom: 30px;
}
@media (min-width: 768px) {
  .form__wrapper {
    margin-bottom: 49px;
  }
  .form__lead {
    margin-bottom: 63px;
  }
  .form__list {
    width: 504px;
  }
  .form__label {
    font-size: 19px;
    line-height: 18px;
  }
  .form__label--set {
    margin-left: 62px;
  }
  .form__radio-input + .form__radio-label::before {
    left: 10px;
    top: 4px;
    width: 42px;
    height: 42px;
  }
  .form__radio-input:checked + .form__radio-label::after {
    left: 24px;
    top: 18px;
  }
  .form__item--good-type:not(:last-child) {
    margin-bottom: 40px;
  }
  .form__item--colour-type {
    margin-bottom: 38px;
    width: 242px;
  }
  .form__item--colour-type:nth-last-child(2) {
    margin-bottom: 0;
  }
  .form__list--colour-type {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 23px;
    padding-top: 4px;
    justify-content: space-between;
  }
  .form__checkbox-button + .form__checkbox-label::before {
    width: 49px;
    height: 47px;
    left: 5px;
    top: -4px;
  }
  .form__checkbox-button:checked + .form__checkbox-label::after {
    left: 16px;
    top: 11px;
    width: 32px;
    height: 22px;
    background-image: url("../img/checkbox-tick-tablet.svg");
  }
  .form__checkbox-label {
    margin-left: 86px;
  }
  .form__item--user-name {
    flex-direction: row;
    border: none;
    margin-bottom: 39px;
  }
  .form__item--user-name-last {
    flex-direction: row;
    border: none;
  }
  .form__label--user-name {
    margin-bottom: 0;
    margin-right: 27px;
    padding-left: 4px;
  }
  .form__radio-label {
    padding-top: 18px;
    padding-left: 21px;
    width: 100%;
  }
  .form__data-svg {
    right: 25px;
  }
  .form__data-svg--email {
    right: 22px;
  }
  .form__input-container--phone,
  .form__input-container--email {
    font-size: 19px;
    line-height: 18px;
  }
  .form__input-container--phone::after {
    right: 26px;
  }
  .form__input-container--email::after {
    right: 21px;
  }
  .form__data--special {
    width: 500px;
    padding-left: 17px;
    padding-bottom: 18px;
  }
